.full-width {
  width: 100%;
}

.full-width-container {
  width: 100%;
  padding: 0;
  margin: 0;
  border: 1px solid red;
}

.full-width-container canvas {
  width: 100% !important;
  height: auto !important;
}

.full-width-container canvas img {
  width: 100% !important;
  height: auto !important;
}

.card {
  box-shadow: inset 0 4px 8px rgb(0 0 0 / 30%);
  border-radius: 20px;
}

.custom-container {
  display: flex;
}

.custom-card-image {
  display: flex;
  align-items: center;
  padding: 10%;
  flex-direction: column;
}

@keyframes slideInRight {
  from {
    transform: translateX(100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

.slide-in {
  animation: slideInRight 0.5s ease-out;
}

.expand-down {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease-in-out, opacity 0.5s ease-in-out;
  opacity: 0;
}

.expand-down.show {
  max-height: 1000px;
  opacity: 1;
}

.main-container {
  margin-top: 40px;
  padding: 20px;
  background-color: white;
  border-radius: 12px;
  border: 1px solid #ddd;
  box-shadow: 0 4px 20px rgb(0 0 0 / 10%);
  max-width: 800px;
  margin-bottom: 50px;
}

.page-footer {
  padding-top: 10px;
  padding-bottom: 10px;
}

.page-footer .container {
  padding-bottom: 10px;
}

footer a {
  font-size: 1.1rem;
}

footer h5 {
  font-size: 1.6rem;
  margin-bottom: 0.5rem;
}
